<script>
import { useAppOptionStore } from '@/apps/status/stores/app-option';
import Cookies from 'js-cookie';

const appOption = useAppOptionStore();

export default {
	data() {
		return {
			year: (new Date()).getFullYear(),
		}
	},
	mounted() {
		appOption.appHeaderHide = true;
		appOption.appFooter = false;
		appOption.appContentClass = 'p-0';
	},
	beforeUnmount() {
		appOption.appHeaderHide = false;
		appOption.appFooter = true;
		appOption.appContentClass = '';
	},
	methods: {
		setLanguage: function (lang) {
			var mode;
			switch (lang) {
				case 'german':
					mode = "de";
					break;
				case 'french':
					mode = "fr";
					break;
				case 'spanish':
					mode = "es";
					break;
				case 'greek':
					mode = "gr";
					break;
				case 'turkish':
					mode = "tr";
					break;
				default:
					mode = "en";
					break;
			}
			const cookieName = 'uslang';
			const cookieOptions = {
				domain: '.unisuite.de',
				path: '/',
				sameSite: 'None',
				secure: true,
				expires: 365
			};
			Cookies.set(cookieName, mode, cookieOptions);
			this.$router.push('/');
		}
	}
}
</script>

<template>
	<div class="container d-flex justify-content-center align-items-center" style="height: 100vh;">
		<card>
			<card-body class="align-items-center text-inverse m-5px bg-inverse bg-opacity-10">
				<h2 class="text-center mb-4">Choose your language...</h2>
				<div class="row chooselang">
					<div class="col-12 text-center mb-2">
						<div class="icon-container" @click="setLanguage('german')">
							<img src="/assets/img/countrys/germany.svg" alt="Germany icon" class="icon-big" />
							<span class="overlay-big">Deutsch</span>
						</div>
						<div class="icon-container" @click="setLanguage('english')">
							<img src="/assets/img/countrys/usa.svg" alt="USA icon" class="icon-big" />
							<span class="overlay-big">English</span>
						</div>
					</div>
					<div class="col-12 text-center" disabled>
						<div class="icon-container" @click="setLanguage('french')">
							<img src="/assets/img/countrys/france.svg" alt="France icon" class="icon" />
							<span class="overlay">Français</span>
						</div>
						<div class="icon-container" @click="setLanguage('spanish')">
							<img src="/assets/img/countrys/spain.svg" alt="Spain icon" class="icon" />
							<span class="overlay">Español</span>
						</div>
						<div class="icon-container" @click="setLanguage('greek')">
							<img src="/assets/img/countrys/greece.svg" alt="Greece icon" class="icon" />
							<span class="overlay">Ελληνική</span>
						</div>
						<div class="icon-container" @click="setLanguage('turkish')">
							<img src="/assets/img/countrys/turkey.svg" alt="Turkey icon" class="icon" />
							<span class="overlay">Türkçe</span>
						</div>
					</div>
					<small class="text-warning text-opacity-50 text-center mt-4">
						<i class="fa fa-circle-info"></i> Manche Sprachen sind aufgrund fehlender Übersetzungen
						deaktiviert.
					</small>
				</div>
			</card-body>
		</card>
	</div>
	<div class="fixed-bottom mb-2 text-center small text-inverse text-opacity-50">
		Copyright &copy; {{ year }} <a href="https://unisuite.de/" target="_blank">UniSuite</a> - All Right
		Reserved.
	</div>
</template>

<style>
div[disabled] {
	pointer-events: none;
	opacity: 0.5;
}
</style>